.katex {
	overflow: auto;
}

.math-q .katex {
	padding: 0 !important;
	height: 5em;
}

.math-a .katex {
	height: 3em;
	display: unset !important;
}

.col {
	border: 0.25em solid transparent;
	color: #455a64;
	background: #eceff1;
	border-radius: 2em;
	margin: 0.5em;
	transition: 250ms;
}

.selected {
	border: 0.25em solid #3f51b5;
	color: #303f9f;
	background: #c5cae9;
}

.selected-correct {
	border: 0.25em solid transparent;
	color: white;
	background: #00c853;
}

.unselected-correct {
	border: 0.25em solid transparent;
	color: white;
	background: #d50000;
}

@media (hover: hover) {
	.col:not(.selected):not(.locked):hover {
		color: #827717;
		background: #fff9c4;
		box-shadow: 0 0 1em rgba(130, 119, 23, 0.5);
	}
}
